	module.exports = {"data":{"postsJson":{"title":"Introducción a 2 Tesalonicenses","questionsTitle":null,"display":"none","bookName":"2 Tesalonicenses","ref":null,"html":"<h6>Persevera</h6><p>El mensaje de la venida de Cristo anima a los creyentes a perseverar</p><h6>Cómo leer 2 Tesalonicenses</h6><p>¿Qué está sucediendo? A veces el mundo alrededor nuestro parece caótico e impredecible. Esta carta brinda algo estable, una perspectiva eterna con la cual podemos evaluar los estándares cambiantes de la sociedad. Las palabras de Pablo nos recuerdan que mientras el mundo avanza hacia su punto final, nuestra esperanza en la eternidad nos fortalece para vivir cada día en un ambiente anticristiano.<br />   El capítulo 1 anima a los cristianos que sufren persecución a perseverar en medio de las dificultades. Los capítulos 2 y 3 confrontan los falsos rumores que anunciaban que los últimos tiempos ya habían pasado. Estos capítulos revelan que deben ocurrir ciertas cosas antes del retorno de Jesús, e instan a confiar firmemente en que Dios cumplirá su plan de redención. La iglesia es exhortada a tener paciencia y firmeza, y a perseverar fielmente en un servicio diligente hasta que Jesús regrese.<br />   Descubrirás varios temas paralelos a los de la primera carta de Pablo a los tesalonicenses: el sufrimiento (1Te 2:14-16; 2Te 1:3-12); el trabajo (1Te 4:9-12; 5:14; 2Te 3:6-15); y los últimos tiempos (1Te 4:13–5:11; 2Te 2:1-12). Esta carta, junto con 1 Tesalonicenses nos aporta mucho de lo que sabemos acerca del regreso del Señor.</p><h6>¿Quién y cuándo escribió esta carta?</h6><p>El apóstol Pablo la escribió a principios de los años 50 d. C., poco después de escribir 1 Tesalonicenses.</p><h6>¿Para quién y por qué se escribió?</h6><p>Pablo les escribió a los creyentes en Tesalónica, quienes necesitaban una mayor dosis de consejo que el recibido en su primera carta.</p><h6>Perspectiva de SourceView</h6><p>Leer la carta de Pablo a los creyentes tesalonicenses es como escuchar un solo lado de una conversación telefónica. La carta está impresa en letra cursiva para dar la sensación de escritura a mano y permitirnos revivir la experiencia de leer esta carta divinamente inspirada.<br />","path":"/394","Question1":null,"Question2":null,"Question3":null,"Question4":null,"prevPath":"/393","nextPath":"/395"}},"pathContext":{}}

